import { gsap } from 'gsap';
import React, { useEffect, useRef, useState } from 'react';
import { useModalStore } from '../store/modalStore';
import { projects } from '../data/projects';

const Main: React.FC = () => {
  const textRefs = useRef<HTMLSpanElement[][]>([]);
  const videoRef = useRef<HTMLVideoElement>(null);
  const workListRef = useRef<HTMLUListElement>(null);
  const animatedSections = useRef<Set<number>>(new Set());
  const { isOpen: isModalOpen } = useModalStore();
  const [currentSection, setCurrentSection] = useState(0);
  const [currentProject, setCurrentProject] = useState(0);
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
  const isAnimating = useRef(false);
  const totalProjects = projects.length;

  //     
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  //  14 
  useEffect(() => {
    const video = videoRef.current;
    if (video) {
      const handleLoadedMetadata = () => {
        video.currentTime = 15;
      };
      video.addEventListener('loadedmetadata', handleLoadedMetadata);
      //   
      if (video.readyState >= 1) {
        video.currentTime = 15;
      }
      return () => {
        video.removeEventListener('loadedmetadata', handleLoadedMetadata);
      };
    }
  }, []);

  //  
  const splitText = (text: string): React.ReactElement[] => {
    const spanGroup: HTMLSpanElement[] = [];
    const elements = text.split('').map((char: string, index: number) => (
      <span
        key={index}
        className="split-char"
        ref={el => {
          if (el) {
            spanGroup.push(el);
          }
        }}
      >
        {char === ' ' ? '\u00A0' : char}
      </span>
    ));
    textRefs.current.push(spanGroup);
    return elements;
  };

  //   
  const goToSection = (index: number) => {
    const sections = document.querySelectorAll('.panel');
    if (index < 0 || index >= sections.length || isAnimating.current) return;

    isAnimating.current = true;
    setCurrentSection(index);

    gsap.to(window, {
      scrollTo: { y: sections[index], autoKill: false },
      duration: 0.8,
      ease: 'power2.inOut',
      onComplete: () => {
        isAnimating.current = false;
      },
    });
  };

  //    
  const goToProject = (index: number) => {
    if (index < 0 || index >= totalProjects || isAnimating.current) return;

    isAnimating.current = true;
    setCurrentProject(index);

    gsap.to(workListRef.current, {
      x: -index * window.innerWidth,
      duration: 0.8,
      ease: 'power2.inOut',
      onComplete: () => {
        isAnimating.current = false;
      },
    });
  };

  useEffect(() => {
    // GSAP ScrollTo   
    import('gsap/ScrollToPlugin').then(({ ScrollToPlugin }) => {
      gsap.registerPlugin(ScrollToPlugin);
    });

    const sections = document.querySelectorAll('.panel');
    const totalSections = sections.length;

    //    (768px  )
    const handleWheel = (e: WheelEvent) => {
      e.preventDefault(); // 768px     

      if (isMobile || isModalOpen) return;

      if (isAnimating.current) return;

      //   ( 2)   
      if (currentSection === 2) {
        if (e.deltaY > 0) {
          //     
          if (currentProject < totalProjects - 1) {
            goToProject(currentProject + 1);
          } else {
            //    
            goToSection(currentSection + 1);
          }
        } else if (e.deltaY < 0) {
          //     
          if (currentProject > 0) {
            goToProject(currentProject - 1);
          } else {
            //    
            goToSection(currentSection - 1);
          }
        }
        return;
      }

      if (e.deltaY > 0 && currentSection < totalSections - 1) {
        //  
        goToSection(currentSection + 1);
      } else if (e.deltaY < 0 && currentSection > 0) {
        //  
        goToSection(currentSection - 1);
      }
    };

    //    (768px  )
    const handleKeyDown = (e: KeyboardEvent) => {
      if (isMobile || isModalOpen || isAnimating.current) return;

      //    
      if (currentSection === 2) {
        if (e.key === 'ArrowDown' || e.key === 'PageDown' || e.key === 'ArrowRight') {
          e.preventDefault();
          if (currentProject < totalProjects - 1) {
            goToProject(currentProject + 1);
          } else {
            goToSection(currentSection + 1);
          }
        } else if (e.key === 'ArrowUp' || e.key === 'PageUp' || e.key === 'ArrowLeft') {
          e.preventDefault();
          if (currentProject > 0) {
            goToProject(currentProject - 1);
          } else {
            goToSection(currentSection - 1);
          }
        }
        return;
      }

      if ((e.key === 'ArrowDown' || e.key === 'PageDown') && currentSection < totalSections - 1) {
        e.preventDefault();
        goToSection(currentSection + 1);
      } else if ((e.key === 'ArrowUp' || e.key === 'PageUp') && currentSection > 0) {
        e.preventDefault();
        goToSection(currentSection - 1);
      }
    };

    //   ()
    let touchStartY = 0;
    let touchStartX = 0;
    const handleTouchStart = (e: TouchEvent) => {
      touchStartY = e.touches[0].clientY;
      touchStartX = e.touches[0].clientX;
    };

    const handleTouchEnd = (e: TouchEvent) => {
      if (isModalOpen || isAnimating.current) return;

      const touchEndY = e.changedTouches[0].clientY;
      const touchEndX = e.changedTouches[0].clientX;
      const diffY = touchStartY - touchEndY;
      const diffX = touchStartX - touchEndX;

      //     
      if (currentSection === 2) {
        //      
        if (Math.abs(diffX) > Math.abs(diffY) && Math.abs(diffX) > 50) {
          if (diffX > 0 && currentProject < totalProjects - 1) {
            goToProject(currentProject + 1);
          } else if (diffX < 0 && currentProject > 0) {
            goToProject(currentProject - 1);
          }
          return;
        }
        //  
        if (diffY > 50) {
          if (currentProject < totalProjects - 1) {
            goToProject(currentProject + 1);
          } else {
            goToSection(currentSection + 1);
          }
        } else if (diffY < -50) {
          if (currentProject > 0) {
            goToProject(currentProject - 1);
          } else {
            goToSection(currentSection - 1);
          }
        }
        return;
      }

      if (diffY > 50 && currentSection < totalSections - 1) {
        goToSection(currentSection + 1);
      } else if (diffY < -50 && currentSection > 0) {
        goToSection(currentSection - 1);
      }
    };

    window.addEventListener('wheel', handleWheel, { passive: false });
    window.addEventListener('keydown', handleKeyDown);
    window.addEventListener('touchstart', handleTouchStart, { passive: true });
    window.addEventListener('touchend', handleTouchEnd, { passive: true });

    //  
    return () => {
      window.removeEventListener('wheel', handleWheel);
      window.removeEventListener('keydown', handleKeyDown);
      window.removeEventListener('touchstart', handleTouchStart);
      window.removeEventListener('touchend', handleTouchEnd);
    };
  }, [currentSection, currentProject, isModalOpen, isMobile]);

  //     
  useEffect(() => {
    //     ( 1: 0-2,  2: 3,  3: 4)
    const sectionTextMap: { [key: number]: number[] } = {
      1: [0, 1, 2], // about  3
      2: [3],       // work  
      3: [4],       // contact  
    };

    const groupIndices = sectionTextMap[currentSection];
    if (!groupIndices || animatedSections.current.has(currentSection)) return;

    animatedSections.current.add(currentSection);

    groupIndices.forEach((index) => {
      const group = textRefs.current[index];
      if (!group || group.length === 0) return;

      gsap.set(group, {
        y: 120,
        opacity: 0,
        rotateX: -90,
      });

      gsap.to(group, {
        y: 0,
        opacity: 1,
        rotateX: 0,
        duration: 1.2,
        stagger: {
          each: 0.03,
          from: 'start',
        },
        ease: 'power4.out',
        delay: 0.3,
      });
    });
  }, [currentSection]);

  return (
    <div className="wrap">
      {/*   */}
      <section className="panel intro-section">
        <video
          ref={videoRef}
          autoPlay
          loop
          muted
          playsInline
          className="about-video"
          aria-label=" "
        >
          <source src={require('../assets/videos/video.mp4')} type="video/mp4" />
        </video>
      </section>

      {/*   */}
      <section id="about" className="panel about-section" aria-label="">
        <div className="about-container">
          <div className="about-text">
            <p className="text">{splitText('  ')}</p>
            <p className="text">{splitText(' ')}</p>
            <p className="text">{splitText('')}</p>
          </div>

          <div className="about-description">
            3         ,      .       ,     UI     .
 JavaScript React       .  CSS        ,      .
          </div>
        </div>
      </section>

      {/*   */}
      <section id="news" className="panel work-section" aria-label="">
        <div className="work-container">
          <h2 className="work-title">{splitText('Projects')}</h2>

          <ul className="work-list" role="list" ref={workListRef}>
            {projects.map(project => (
              <li key={project.id} className="work-slide">
                {/* :   */}
                <div className="work-image-area">
                  {project.image && (
                    project.image.endsWith('.mp4') ? (
                      <video
                        src={project.image}
                        autoPlay
                        loop
                        muted
                        playsInline
                        className="work-thumbnail"
                      />
                    ) : (
                      <img
                        src={project.image}
                        alt={project.title}
                        className="work-thumbnail"
                      />
                    )
                  )}
                </div>

                {/* :   */}
                <div className="work-info-area">
                  <h3 className="work-project-title">{project.title}</h3>
                  <div className="work-description-box">
                    <p>{project.description}</p>
                  </div>
                  {project.tech && project.tech.length > 0 && (
                    <ul className="work-tech-list">
                      {project.tech.map((tech, index) => (
                        <li key={index}>{tech}</li>
                      ))}
                    </ul>
                  )}
                  {project.link && (
                    <a
                      href={project.link}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="work-link"
                    >
                       
                      <i className="ico ico-arrow" aria-hidden="true"></i>
                    </a>
                  )}
                </div>
              </li>
            ))}
          </ul>

        </div>
      </section>

      {/*   */}
      <section id="contact" className="panel contact-section" aria-label="">
        <div className="contact-container">
          <h2 className="contact-title">{splitText('     ?')}</h2>

          <div className="contact-info">
            <p className="contact-email">Email : juyeop198@naver.com</p>

            <p className="contact-phone">Tel: 010-5917-7297</p>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Main;
